import { getAuth } from "@clerk/remix/ssr.server";
import { json, LoaderArgs, redirect } from "@remix-run/node";
import { useLoaderData } from "@remix-run/react";
import DashHeader from "~/components/dash-header";
import { getTotalDashboard } from "~/db/dashboard.server";

export async function loader({ request }: LoaderArgs) {
    const { userId } = await getAuth(request);
    
    if(!userId) {
        return redirect("/sign-up");
    }

    const { totalCategories, totalTips, totalStats } = await getTotalDashboard(userId);
    return json({ totalCategories, totalTips, totalStats });
}

export default function DashboardIndexRoute(){
    const { totalCategories, totalTips, totalStats } = useLoaderData<typeof loader>();

    return (
        <div>
            <DashHeader title="Home" />
            <div className="max-w-4xl m-auto mt-6">
                <section className="md:flex md:gap-4">
                    <div className="text-gray-900 border-2 border-gray-800 rounded-lg px-5 py-2.5 text-center mb-3 md:flex-1">
                        <h4 className="font-semibold text-lg mb-3">Total Categories</h4>
                        <p className="font-extrabold text-3xl">{totalCategories}</p>
                    </div>

                    <div className="text-gray-900 border-2 border-gray-800 rounded-lg px-5 py-2.5 text-center mb-3 md:flex-1">
                        <h4>Total Tips</h4>
                        <p>{totalTips}</p>
                    </div>

                    <div className="text-gray-900 border-2 border-gray-800 rounded-lg px-5 py-2.5 text-center mb-3 md:flex-1">
                        <h4>Total Request Today</h4>
                        <p>{totalStats}</p>
                    </div>
                </section>
            </div>
        </div>
    )
}